import React, {Component} from 'react';
import Modal from '../components/Signin/';


class Index extends Component {
  constructor() {
    super();
    this.state = {
      loggedIn: true,
    };
  }
  render() {
    return (
      <div>
        { this.state.loggedIn ? <Modal/> : null }
      </div>
    )
  }
}
export default Index;
